<template>
  <div class="py-spacing-100">
    <div class="mb-spacing-100 flex items-center gap-spacing-100">
      <h3 class="text-body/l/relaxed/500 text-content-on-neutral-primary">
        Products
      </h3>
      <div
        @click="handleAddClick()"
        class="flex cursor-pointer items-center justify-center"
      >
        <IconPlus
          class="h-spacing-250 w-spacing-250 text-content-on-neutral-primary"
        />
      </div>
    </div>
    <p class="text-body/m/relaxed/400 text-content-on-neutral-secondary">
      <span v-for="(category, index) in this.getProducts" :key="index">
        <span v-if="index > 0" class="text-content-on-neutral-secondary">
          |
        </span>
        <span
          class="cursor-pointer hover:text-content-on-neutral-primary"
          @click="goToProductPage(category?._id)"
          >{{ category?.display_name }}</span
        >
      </span>
    </p>
  </div>
</template>

<script>
export default {
  props: {
    company: {
      type: Object,
      required: false,
      default: () => ({}),
    },
  },
  computed: {
    getProducts() {
      let output = [];
      if (this.company?.products) {
        output = this.company?.products
          ?.map((item) => ({
            display_name: item?.productId?.title,
            _id: item?.productId?._id,
          }))
          ?.filter((item) => item?.display_name);
      }
      return output;
    },
  },
  components: {
    IconPlus: () => import("@/components/svgIcons/plus.vue"),
  },
  methods: {
    handleAddClick() {
      this.$bus.$emit("update-category-to-company-modal", {
        showModal: true,
        companyId: this.company?._id,
        companyName: this.company?.companyName,
        products: this.getProducts,
      });
    },
    goToProductPage(id) {
      this.$bus.$emit("product-details-table-modal", {
        showModal: true,
        productId: id,
      });
    },
  },
};
</script>

<style scoped></style>
